import {Core, Fetch} from '@true/core';
import {batch} from 'react-redux';
import {ICreateTopic, ITopic} from '@/store/topics/type';
import {setTopicsQuery, syncTopics} from '@/store/topics/index';

export const requestTopicsChannel = async (params: {path: string}) => {
  const {data} = await Fetch.postWithToken<{
    topics: ITopic[];
  }>(
    `@${Core.systemPath}/inside/ajax/api/channel/update/topic?path=${params.path}`,
  );
  batch(() => {
    if (data.topics.length) {
      syncTopics(data.topics);
      setTopicsQuery(prev => ({
        ...prev,
        [params.path]: data.topics.map(item => item.id.toString()),
      }));
    }
  });
  return data.topics;
};
export const requestMyTopicsChannel = async (params: {path: string}) => {
  const {data} = await Fetch.postWithToken<{
    topics: ITopic[];
  }>(
    `@${Core.systemPath}/inside/ajax/api/channel/update/topic?path=${params.path}&created=mine`,
  );
  batch(() => {
    if (data.topics.length) {
      syncTopics(data.topics);
      setTopicsQuery(prev => ({
        ...prev,
        [`created_${params.path}`]: data.topics.map(item => item.id.toString()),
      }));
    }
  });
  return data.topics;
};
export const requestCreateTopic = async (params: ICreateTopic) => {
  const {data} = await Fetch.postWithToken<{topic: ITopic}>(
    `@${Core.systemPath}/inside/ajax/api/topic/create`,
    params,
  );
  batch(() => {
    if (data.topic) {
      syncTopics([data.topic]);
      setTopicsQuery(prev => ({
        ...prev,
        ['all']: [data.topic.id.toString(), ...(prev['all'] || [])],
      }));
    }
  });
};

export const requestEditTopic = async (params: ICreateTopic) => {
  const {data} = await Fetch.postWithToken<{topic: ITopic}>(
    `@${Core.systemPath}/inside/ajax/api/topic/edit`,
    params,
  );
  batch(() => {
    if (data.topic) {
      syncTopics([data.topic]);
    }
  });
};
