import React, {memo, RefObject} from 'react';
import {Colors, Fonts, styled, useMe} from '@true/core';
import {IC_OUTLINED_EXPAND_MORE} from '@true/asset';
import {Avatar, UIButton, UIText, UITextStyle} from '@true/ui';
import {BottomSheetModal} from '@gorhom/bottom-sheet';
import {globalStyles} from '@/utils/globalStyle';
import {
  useAnimatedStyle,
  useSharedValue,
  useWorkletCallback,
} from 'react-native-reanimated';
import {LayoutChangeEvent} from 'react-native';

export const AvatarWithSelectChannel = memo(function AvatarWithSelectChannel({
  bottomSheetRef,
  label,
  typePost,
}: {
  bottomSheetRef: RefObject<BottomSheetModal>;
  label: string;
  typePost: string;
}) {
  const me = useMe();
  const widthButton = useSharedValue(200);

  const animatedStyled = useAnimatedStyle(
    () => ({
      width: widthButton.value,
    }),
    [],
  );
  const onViewLayout = useWorkletCallback(
    (e: LayoutChangeEvent) => {
      widthButton.value = e.nativeEvent.layout.width;
    },
    [widthButton],
  );

  return (
    <SRow>
      <Avatar size={48} userKey={me?.id || '0'} disableClick={true} />
      <SView>
        <UIText.BodyMedium500 onLayout={onViewLayout}>
          {me?.name} create {typePost}
        </UIText.BodyMedium500>
        <SButtonSelect
          style={animatedStyled}
          title={label ? label : 'Select channel'}
          onPress={() => {
            bottomSheetRef.current?.present();
          }}>
          <SExpandIcon source={IC_OUTLINED_EXPAND_MORE} />
        </SButtonSelect>
      </SView>
    </SRow>
  );
});

const SRow = styled.View`
  flex-direction: row;
  align-items: center;
  gap: 12px;
`;

const SView = styled.View`
  justify-content: center;
  gap: 4px;
`;
const SButtonSelect = styled(UIButton).attrs(p => ({
  textStyle: {
    ...UITextStyle.body_medium_400,
    color: p.theme.neutral2,
    width: '85%',
  },
  textProps: {
    numberOfLines: 1,
  },
}))`
  flex-direction: row;
  border-radius: 40px;
  border-width: 1px;
  border-color: ${p => p.theme.neutral5};
  justify-content: space-between;
  align-items: center;
  height: 32px;
  padding-left: 16px;
  padding-right: 8px;
`;

const SExpandIcon = styled.Image`
  width: 24px;
  height: 24px;
  tint-color: ${p => p.theme.neutral2};
`;
